import React, { Component } from "react";

import './App.css';
import './Styling.css'
import WhyPaul from './components/WhyPaul'
import SecurityInfo from './components/SecurityInfo'
import About from './components/About'
import GetStarted from './components/GetStarted'
import Team from './components/Team'
import Whitepaper from './components/Whitepaper'

import Footer from './components/Footer'
import {config} from './Config'
import {PublicClientApplication} from '@azure/msal-browser'
import {  Menu, Image, Button, Icon, Grid } from 'semantic-ui-react'
import logo from './images/logo.png'
import SearchEng from './components/SearchEng'
import Profile from './components/profile'

class App extends Component{



  constructor (props)
{
  super(props);
  const userkey = localStorage.getItem('userkey');
  this.state={
    error: null,
    
    isAuthenticated: userkey,
    user:{},
    profilenum:0
  };

  this.login = this.login.bind(this)
  this.logout = this.logout.bind(this)

  this.profile = this.profile.bind(this)

  this.publicClientApplication = new PublicClientApplication({
    auth:
    {
      clientId: config.appId,
      redirectUri: config.redirectUri,
      authority: config.authority
    },
    cache:
    {
      cacheLocation: "sessionStorage",
      storeAuthStateInCookie: true
    }
  });
}

async login()
{
  try{
    await this.publicClientApplication.loginPopup(
      {
        
        scopes: config.scopes,
        prompt: "select_account"
      }
      
    );
    this.setState({isAuthenticated:true})
    localStorage.setItem('userkey', true);
  }
  catch(err)
  {
    this.setState({
      isAuthenticated: false,
      user: {},
      error: err
    })
  }
}

logout()
{
  this.setState({
    isAuthenticated: false,
    user: {},
  })
  localStorage.removeItem('userkey');

  this.publicClientApplication.logoutRedirect();
}

profile()
{
  
  this.setState({profilenum: (this.state.profilenum + 1)%2 })
  
}

render(){
  let window 

  
  if(this.state.isAuthenticated){
    window = <div className='mg'>
      <div className='mg'>
        <Menu  secondary>
        
          <Image src={logo} circular size='mini'/>

          <Menu.Menu >
           
           
           <Menu.Item
            name=''
          />
            <Button icon labelPosition='left' basic color='blue' onClick={()=> this.profile()}>
              <Icon name='user' />
              Profile
            </Button>

            
           
          </Menu.Menu>
          
          
          <Menu.Menu position='right'>

          <SearchEng/>
          <Menu.Item
            name=''
          />
          <Button size='medium' basic color='blue' onClick={()=> this.logout()}>Logout</Button>

           
          </Menu.Menu>
        </Menu>
        </div>

        
      </div>
  }
  else{
  window = 
   <div >


    <div className='mg'>

    <div className='mg'>
    <Menu  secondary>
          
          <Image src={logo}  circular size='mini'/>
          <Menu.Menu position='right'>
           
           <SearchEng/>
           <Menu.Item
            name=''
          />
            <Button size='medium' basic color='blue' onClick={()=> this.login()}>Login</Button>
            
           
          </Menu.Menu>
        </Menu>
        </div>
        <br />
      <div >
      <WhyPaul />
      </div>
      <br/>

      <SecurityInfo/>

      <br />

      <GetStarted />

      <div className='mg'> 
      <About />
      </div>

      <div >
        <Team/>
      </div>

      <div >
        <Whitepaper/>
      </div>

        
        </div>
        <Footer/>

    </div>

  }

  let profile

  if(this.state.profilenum)
  {
    profile = <Profile/>
  }

  else
  {
    profile = <div></div>
  }


  return (
    <div className='mg'>
      {window}

      <Grid centered columns={2}>
    <Grid.Column>
      {profile}
    </Grid.Column>

    
    
  </Grid>

    </div>
  );
}

}

export default App;
