import React, { Component } from 'react'
import { Form } from 'semantic-ui-react'



class Profile extends Component {
  
    constructor(props)
    {
        super(props)

        this.state =
        {
            username: '',
            email:'',
            isCheckbox: 0
        }
    }

  handleChange = (e, { value }) => this.setState({ value })

  handleNameChange = (event) =>
    {
        this.setState({
            username: event.target.value
        })
    }

    handleMailChange = (event) =>
    {
        this.setState({
            email: event.target.value
        })
    }

    
    onSubmit = event  =>
    {
        alert(`${this.state.value}`)
        //Azure Database
    }

    checkboxClick= event => 
    {
        this.setState({isCheckbox : (this.state.isCheckbox+1)%2})

        
    }
    


  render() {
    const { value } = this.state

    let checkboxdiv

    if(this.state.isCheckbox===1 && this.state.username!=='' && this.state.email!=='' && this.state.value!==undefined)
        checkboxdiv = <Form.Button onClick={this.onSubmit}>Submit</Form.Button>
    else
        checkboxdiv = <Form.Button disabled onClick={this.onSubmit}>Submit</Form.Button>


    return (
      <Form>
        <Form.Group widths='equal'>
          <Form.Input fluid label='Name' placeholder='User Name' value ={this.state.username} onChange={this.handleNameChange}/>
          <Form.Input fluid label='Azure Mail ID' placeholder='Email ID' value ={this.state.email} onChange={this.handleMailChange}/>
          
        </Form.Group>
        <Form.Group inline>
          <label>User Type</label>
          <Form.Radio
            label='Student'
            value='st'
            checked={value === 'st'}
            onChange={this.handleChange}
          />
          <Form.Radio
            label='CA'
            value='ca'
            checked={value === 'ca'}
            onChange={this.handleChange}
          />
          <Form.Radio
            label='University'
            value='un'
            checked={value === 'un'}
            onChange={this.handleChange}
          />
        </Form.Group>
        <Form.TextArea label='About' placeholder='Tell us more about you... (not compulsory)' />
        <Form.Checkbox label='I agree to the Terms and Conditions' onChange={this.checkboxClick}/>
        {checkboxdiv}
      </Form>
    )
  }
}

export default Profile